.cart-page-container {
    min-height: 100vh;
  }
  
  .cart-body-container {
    flex: 1;
  }
  
  .shop-now-btn {
    background-color: #0b69ff;
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 12px;
    border-radius: 8px;
    border: none;
    padding-left: 16px;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-right: 16px;
    outline: none;
    cursor: pointer;
  }
  
  .empty-view-image {
    width: 80%;
    max-width: 450px;
    margin-bottom: 25px;
  }
  
  .empty-description {
    font-size: 18px;
    font-weight: 500;
    font-family: 'Roboto';
  }
  
  .cart-items-header {
    padding: 25px 0;
    width: 80%;
    margin: 0 auto;
  }
  
  .cart-items-header h1 {
    font-size: 25px;
    font-family: 'Roboto';
    font-weight: 500;
  }
  
  .remove-all-btn {
    font-size: 14px;
    font-family: 'Roboto';
    font-weight: 500;
    padding: 8px 16px;
    background-color: transparent;
    border: none;
    outline: none;
  }
  